.filter {
  
}

.filter .toggle-filter {
  text-align: right; 
  color: #bbb;
  padding: 15px;
  -webkit-transition: .3s;
  -o-transition: .3s;
  transition: .3s;
  font-weight: 600;
}

.filter .toggle-filter:hover {
  cursor: pointer;
  color: #121212;
}

.filter .toggle-filter .filter-icon {
  -webkit-transition: .3s;
  -o-transition: .3s;
  transition: .3s;
  display: inline-block;
  width: 10px;
  height: 2px;
  margin: 5px;
  background-color: #bbb;
}

.filter .toggle-filter .filter-icon::before {
  -webkit-transition: .3s;
  -o-transition: .3s;
  transition: .3s;
  content: "";
  position: absolute;
  background-color: #bbb;
  display: block;
  width: 15px;
  height: 2px;
  margin: 5px;
  -webkit-transform: translate(-50%, -10px);
      -ms-transform: translate(-50%, -10px);
          transform: translate(-50%, -10px);
}

.filter .toggle-filter .filter-icon::after {
  -webkit-transition: .3s;
  -o-transition: .3s;
  transition: .3s;
  content: "";
  position: absolute;
  background-color: #bbb;
  display: block;
  width: 5px;
  height: 2px;
  margin: 5px;
  -webkit-transform: translate(-50%, 0);
      -ms-transform: translate(-50%, 0);
          transform: translate(-50%, 0);
}

.filter .toggle-filter:hover .filter-icon, .filter .toggle-filter:hover .filter-icon::before, .filter .toggle-filter:hover .filter-icon::after {
  background-color: #f95979;
}

.filter .filter-menu {
  display: -ms-flexbox;
  display: flex;
  height: 130px;
  -webkit-transition: height .3s ease-out;
  -o-transition: height .3s ease-out;
  transition: height .3s ease-out;
  -webkit-animation: slide-in 1s forwards cubic-bezier(.69,.26,.36,.87);
          animation: slide-in 1s forwards cubic-bezier(.69,.26,.36,.87);
}

@-webkit-keyframes slide-in {
  0% {
    -webkit-transform: translateY(-80px);
            transform: translateY(-80px);
    margin-bottom: -130px;
    opacity: 0;
  }
  31% {
    opacity: 0;
  }
  100% {
    -webkit-transform: translateY(0px);
            transform: translateY(0px);
    margin-bottom: 0px;
    opacity: 1;
  }
}

@keyframes slide-in {
  0% {
    -webkit-transform: translateY(-80px);
            transform: translateY(-80px);
    margin-bottom: -130px;
    opacity: 0;
  }
  31% {
    opacity: 0;
  }
  100% {
    -webkit-transform: translateY(0px);
            transform: translateY(0px);
    margin-bottom: 0px;
    opacity: 1;
  }
}

.filter .filter-menu.hidden {
  height: 0;
}

.filter .filter-menu {

}

.filter .filter-menu div .category-title {
  color: #121212;
  font-weight: 600;
  font-size: 1.2em;
}

.filter .filter-menu div .category-items {
  margin: 15px 0;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-wrap: wrap;
      flex-wrap: wrap;
  -ms-flex-pack: justify;
      justify-content: space-between;
  color: #121212;
  
}

.filter .filter-menu div .category-items div {
  background-color: #e7e6e4;
  -ms-flex: 1 0 10%;
      flex: 1 0 10%;
  color: #121212;
  font-weight: 600;
  padding: 5px 10px;
  margin: 5px;
  border-radius: 15px;
  -webkit-transition: .3s;
  -o-transition: .3s;
  transition: .3s;
  text-align: center;
}

.filter .filter-menu div .category-items div:hover {
  background-color: #e7e6e4;
  color: #121212;
  cursor: pointer;
}

.filter .filter-menu div .category-items .disabled {
  background-color: rgba(0,0,0,0) !important;
}



