.pokemon-container {
  margin: 2rem 0;
  padding: 0;
  list-style-type: none;
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(10rem, 1fr));
  grid-gap: 1.8rem;
}

.pokemon-container-loading {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
}

.pokemon-container-loading .pokemon {
  display: inline-block;
  width: 10rem;
  height: 10rem;
  -webkit-mask: url('../Pokemon/pokeball.svg') no-repeat 100% 100%;
  mask: url('../Pokemon/pokeball.svg') no-repeat 100% 100%;
  -webkit-mask-size: cover;
  mask-size: cover;
  background-color: #ddd;
  margin: 4rem;
  -webkit-animation: 3s spin infinite cubic-bezier(0.24,-0.35, 0.04, 0.99);
          animation: 3s spin infinite cubic-bezier(0.24,-0.35, 0.04, 0.99);
}

@-webkit-keyframes spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }

  to {
    -webkit-transform: rotate(720deg);
            transform: rotate(720deg);
  }
}

@keyframes spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }

  to {
    -webkit-transform: rotate(720deg);
            transform: rotate(720deg);
  }
}


